function route() {
    console.log(window.location)
    //Ëé∑ÂèñÁî®Êà∑ÊÉ≥ÂéªÂì™Èáå
    let number = window.location.hash.substr(1);
    let app = document.querySelector("#app")

    //Â¶ÇÊûúÂàöÂºÄÂßãËÉΩËé∑ÂæóË∑ØÁî±ÔºåÂ∞±Ëé∑ÂèñÔºåÂ¶ÇÊûúÊ≤°ÊúâÂ∞±ÈªòËÆ§ÊòØ1
    //ÈªòËÆ§Ë∑ØÁî±
    number = number || 1

    //Ëé∑ÂèñÁî®Êà∑ÁïåÈù¢
    let div = document.querySelector(`#div${number}`)

    if (!div) {
        div = document.querySelector("#div404")
    }
    div.style.display = "block";

    if (app.children.length > 0) {
        app.children[0].style.display = "none";
        console.log(app.children.length);
        //ËøôÈáåÊääËé∑ÂèñÂà∞ÁöÑÂú®appÈáåÈù¢ÁöÑÂÜÖÂÆπÈáçÊñ∞Ëé∑ÂèñÂà∞Â°ûÂõûÂà∞bodyÈáåÔºå
        document.body.appendChild(app.children[0])
    }
    app.appendChild(div);

}
route()

//Ê£ÄÊµãÔºåÂ¶ÇÊûúÁΩëÂùÄÂêéÈù¢ÁöÑÂú∞ÂùÄÂèòÂåñ‰∫ÜÔºåÂ∞±ÁõëÂê¨ÂÆÉÔºåÂπ∂ÊâßË°åÂÆö‰πâÁöÑÂáΩÊï∞
window.addEventListener("hashchange", () => {
    console.log("hash Âèò‰∫Ü");
    route();

})